import React, { useState } from "react";
import { Divider, Typography } from "@mui/material";
import ShowMoreText from "react-show-more-text";
import * as C from "./styled";
import Vocation1 from "../../../_assets/img/vocation/vocation1.jpg";
import Vocation2 from "../../../_assets/img/vocation/vocation2.jpg";
import Vocation3 from "../../../_assets/img/vocation/vocation3.jpg";

const Vocation = () => {
  const [expand, setExpend] = useState(false);
  const onClick = () => {
    setExpend(!expand);
  };

  return (
    <>
      <div class="row d-flex justify-content-center">
        <div class="col-9">
          <h1 class="mb-5 text-center font-italic"><Divider>Vocao</Divider></h1>
          <figure class="text-center fs-8">
            <blockquote>
              <b>
                {" "}
                Ele no est longe de cada um de ns, pois nele vivemos, nos
                movemos e existimos.
              </b>
            </blockquote>
            <figcaption class="blockquote-footer">
              <cite title="(At 17, 27)">(At 17, 27)</cite>
            </figcaption>
          </figure>
        </div>
      </div>

      <div class="row mb-3 d-flex justify-content-center">
        <div class="col-6">
          <div class="row">
            <C.Paragraph>
              A fonte de nossa existncia, donde samos para chegar  beleza e a
              turbulncia da vida humana, que se chama Deus. Muito antes de
              sermos criados, a nossa realidade estava j presente no pensamento
              amoroso de Deus: Antes de formar-te no ventre de tua me, Eu o
              conheci; antes que tu nascesse , eu te conhecia e te consagrei,
              para ser meu profeta entre as naes. A raiz profunda que
              sustenta e explica a nossa histria pessoal tem o seu eixo no
              mistrio de uma Presena transcendente, humanamente inexplicvel,
              que enche e abraa a totalidade de nossa existncia.
            </C.Paragraph>

            <C.Paragraph>
              A pessoa humana  uma realidade que vem de longe e vai longe. Na
              nossa caminhada terrestre, temos o implcito dever, o instinto
              congnito e o desejo profundo de construir o dom da vida recebida.
              O<b> infinito</b>  meta de nosso caminho, embora sempre marcado,
              nas concretas realizaes histricas, pelas limitaes da nossa
              prpria natureza e pela graa de Deus que constantemente atua em
              ns. Caminhar corresponde, de qualquer maneira, a abrir os olhos
              para enxergar a autntica verdade do universo, das pessoas, da
              vida e de Deus. A verdade  que: sem Deus nada somos, nem podemos
              fazer, pois se respiramos, falamos, andamos, etc...  por graa
              dele que o fazemos ! Deus... colocou o senso da eternidade no
              corao do homem, para que o homem possa busca-lo, mesmo sem
              compreender a obra que Ele realiza em ns.
            </C.Paragraph>

            <C.Paragraph>
              Este sentido de eternidade  aquela luz que deve emanar da nossa
              vocao:
            </C.Paragraph>

            <figure class="text-center fs-8">
              <blockquote>
                <b>
                  {" "}
                  Pois em Ti se encontra a fonte da vida e com a Tua luz ns
                  veremos a luz.
                </b>
              </blockquote>
              <figcaption class="blockquote-footer">
                <cite title="(Ecl 3,11)">(Ecl 3,11)</cite>
              </figcaption>
            </figure>

            <h3 class="mb-4 fs-5 text-start font-italic">Vocao Religiosa</h3>

            <C.Paragraph>
              Nosso Senhor, que desde toda a eternidade se props criar-nos
              estabeleceu tambm , com todas as circunstncias de tempo e de
              lugar, e caminho que devemos seguir.
            </C.Paragraph>

            <figure class="text-center fs-8">
              <blockquote>
                <b>
                  {" "}
                  Se queres ser perfeito, vai vende teus bens, d-os aos pobres
                  ... Depois, vem e segue-me.
                </b>
              </blockquote>
              <figcaption class="blockquote-footer">
                <cite title="(Mt 9,21)">(Mt 19,21)</cite>
              </figcaption>
            </figure>

            <C.Paragraph>
              Com estas palavras, Nosso Senhor oferece a todos o estado
              Religioso. Santo Toms afirma: Deve-se tomar como dirigido a
              todos o conselho que Jesus Cristo deu ao jovem. Mas para abraar,
              de fato, os Conselhos Evanglicos,  suficiente este chamado
              genrico? <b>NO!</b>responde S. Roberto Belarmino,  preciso ter
              Vocao propriamente dita, ou seja, aquele convite geral deve
              chegar particularizado ao individuo ...por meio da qual o
              indivduo percebe que os conselhos evanglicos constituem o melhor
              caminho para ele e que, por isso, deve abra-los.
            </C.Paragraph>

            <h3 class="mb-4 fs-5 text-start font-italic">
              {" "}
              Como se conhece a vocao?
            </h3>

            <C.Paragraph>
              No se pode esperar que Deus nos aparea, para se ter certeza do
              seu chamado. Nesta terra nossa vida transcorre na f;  atravs de
              pessoas, coisas e acontecimentos que procuramos conhecer a vontade
              de Deus. De certo modo Ele revela esta vontade com muita
              discrio, para que nossa reposta ao seu convite seja mais livre e
              espontnea. Deus parece ter receio de nos constranger, e diminuir
              com isso o mrito de nossa entrega e a generosidade de nosso amor.
            </C.Paragraph>

            <h3 class="mb-4 fs-5 text-start font-italic">
              {" "}
              So sinais de vocao:
            </h3>

            <ol class="list-group list-group-numbered mb-1">
              <li class="list-group-item">
                Uma inclinao espontnea e constante: No se trata de
                sentimento, mas de vontade, um impulso e pensamento que se
                repete irresistivelmente no corao e na mente.
              </li>
              <li class="list-group-item">
                Aptido para o estado religioso: Cada congregao tem suas
                caractersticas, por isso, nem todos so talhados para a mesma
                forma de vida religiosa. A dois requisitos indispensveis e
                comuns a todos os institutos: a pratica das virtudes e o
                esprito de piedade.
              </li>
              <li class="list-group-item">
                Fim reto sobrenatural: Levar uma vida de maior unio com Deus e
                de am-lo sobretudo.
              </li>
            </ol>

            <C.Paragraph>
              Por isso considere atentamente a inteno  vida religiosa. Se
              descobrir que o fim  puramente humano como: levar uma vida mais
              cmoda; livrar-se da "tirania" dos pais; fazer-lhes o agrado
              porque insistem nisso e etc.
              <br />
              Se assim for no  chamado divino, mas sim humano. O fim de um
              chamado divino tem de ser sobrenatural.
            </C.Paragraph>

            <h3 class="mb-4 fs-5 text-start font-italic">
              {" "}
              Vocao Contemplativa
            </h3>

            <h3 class="mb-4 fs-6 bold text-start font-italic">
              {" "}
              <b>Como saber se  um chamado para a Vida Contemplativa?</b>
            </h3>
          </div>

          <div class="row mb-2">
            <div class="d-flex">
              <figure class="m-2">
                <img src={Vocation1} alt="" />
              </figure>
              <div class="p-1">
                <p>
                  Quando vemos pessoas que vo se desligando do mundo e das
                  coisas dele ( falo aqui das coisas boas e lcitas, coisas
                  materiais, de pessoas, lugares...) isto no acontece por
                  acaso,  Deus que s leva a isso, por que as faz descobrir que
                  s Deus satisfaz plenamente o seu corao. Tal pessoa 
                  chamada a seguir a Jesus mais de perto e a estar como Maria
                  aos ps de Jesus (Lc10,38-42) de am-lo mais intimamente e de
                  servi-lo mais de perto e a viver o seu Batismo com mais
                  radicalidade.
                </p>

                <p class="fs-8 text-start font-italic">
                  {" "}
                  <b>Mas com que finalidade?</b>
                </p>

                <ShowMoreText
                  lines={1}
                  more={"Mostrar mais"}
                  less={"Mostrar menos"}
                  onClick={onClick}
                  expanded={expand}
                >
                  <p>
                    Deus quer reservar s para si Almas que Ele escolhe
                    gratuitamente, por sua bondade. Destes ntimos amigos seus,
                    Ele faz intercessores junto a Si, tornando-os canais de sua
                    graa para as outras criaturas. A permanente Orao dos
                    Contemplativos cria-lhes grande proximidade com Deus e
                    confere-lhes um poder especial de intercesso e proteo
                    pelos seus irmos e irms.  esta a razo por que se recorre
                    tanto s suas oraes.
                  </p>

                  <figure class="text-center fs-8">
                    <blockquote>
                      <b> De Deus no se alcana nada, se no  por amor!</b>
                    </blockquote>
                    <figcaption class="blockquote-footer">
                      <cite title="So Joo da Cruz">So Joo da Cruz</cite>
                    </figcaption>
                  </figure>
                </ShowMoreText>
              </div>
            </div>
          </div>

          <div class="mt-4">
            <h3 class="mb-4 fs-6 bold text-start font-italic">
              {" "}
              O que faz o Contemplativo em meio a tantas necessidades do Mundo
              Atual?
            </h3>

            <C.Paragraph>
              O Papa Joo Paulo II responde :  A Vida Contemplativa encontra-se
              no corao da Igreja, e coopera em sua vitalidade, tal como um
              corao em um organismo vivo . Se algum dissesse ao corao:-Que
              est voc fazendo a escondido,sem sair do lugar,calado, enquanto
              as mos trabalham, os ps caminham, a boca fala, etc. Para que
              serve voc ? Ele poderia responder: - Se eu no estivesse aqui
              vivo... nem as mos trabalhariam, nem os ps andariam, a boca no
              falaria, os olhos no veriam. E se dissssemos o mesmo para as
              razes das rvores e os alicerces dos edifcios ? E este o papel
              da Vida Contemplativa na Igreja : Orando com Cristo no monte, com
              Maria no Cenculo, sustentam a Igreja, comunicam a seiva da graa
              Divina a seus membros, fecundam com essa graa o trabalho da
              Pastoral e do Apostolado, para que - como diz a Igreja pelo
              Concilio : Os operrios de Deus no trabalhem em vo.
            </C.Paragraph>

            <h3 class="mb-4 fs-5 bold text-start font-italic">
              {" "}
              A Aventura de Viver Totalmente para Deus!
            </h3>
          </div>

          <div class="row mb-2">
            <div class="d-flex">
              <figure class="m-2">
                <img src={Vocation2} alt="" />
              </figure>
              <div>
                <p>
                  Diante de tudo o que refletimos nos tpicos anteriores,
                  conclumos que: A Vocao  um olhar de predileo que Jesus
                  dirige  alma. Mas, por que ELE escolheu precisamente  TI?
                  Por que te amou com amor de predileo? Por que tratou-a como
                  ao Jovem do Evangelho:{" "}
                  <b>
                    Jesus fixou nele o olhar, amou-o e disse-lhe: Vem e
                    segue-me! Mc 10,21
                  </b>
                </p>
                <ShowMoreText
                  lines={1}
                  more={"Mostrar mais"}
                  less={"Mostrar menos"}
                  onClick={onClick}
                  expanded={expand}
                >
                  <p>
                    Jovem no te detenhas, e no te sintas indigna te tanto
                    amor, porque:{" "}
                    <b>
                      O Olhar de Deus no  como o olhar dos homens ... Deus
                      olha o corao (2 Sm 16,7)
                    </b>
                  </p>

                  <p>
                    Jesus, o Rei do Cus, o Criador das Galxias, e dos espaos
                    infinitos, enamorou-se por TI e te chama. Ests disposta a
                    ser tua Esposa com todas as conseqncias? Por Ele, deves
                    excluir outros amores... pois ELE quer teu corao INTEIRO e
                    que o AMES acima de TODAS as coisas. Ele  ciumento (Ex.
                    20,5). Podes ter amigos, porm s Ele deve ser teu ESPOSO.
                    S ELE que te AMOU na CRUZ, pode pedir-te um amor total at
                    a cruz.
                  </p>
                </ShowMoreText>
              </div>
            </div>
          </div>

          <div class="mt-4">
            <h3 class="mb-4 fs-6 bold text-start font-italic">
              {" "}
              Neste momento, Jesus te olha com carinho e com o <b>
                CORAO
              </b>{" "}
              transbordante de <b>AMOR</b> e te diz:
            </h3>

            <h3 class="mb-4 fs-4 bold text-start font-italic">
              {" "}
              Queres se Consagrar a Mim com todo o teu ser?
            </h3>

            <C.Paragraph>
              Talvez em teu corao surjam dvidas, inseguranas, medo...
            </C.Paragraph>
          </div>

          <div class="row mb-2">
            <div class="d-flex">
              <figure class="m-2">
                <img src={Vocation3} alt="" />
              </figure>
              <div>
                <p>
                  A vida passa rapidamente, e logo viveremos a mesma vida de
                  Deus. Ento veremos novos Cus e outro Sol mais radiante nos
                  iluminar. No seremos mais prisioneiras nesta terra de
                  exlio, tudo haver passado ... O Senhor ser a Alma de nossa
                  Alma e gozaremos da Vida, da verdadeira Vida para sempre.
                  (Sta Teresinha)
                </p>

                <p>
                  <b>Inspiradas e Chamadas por Deus ... ( Sta Regra I )</b>
                </p>

                <p>
                  <b>
                    A Comunidade Concepcionista acolhe cada Irm como um Dom do
                    Senhor.
                  </b>
                </p>
              </div>
            </div>
          </div>

          <div class="mt-4">
            <C.Paragraph>
              Atendendo-a em suas necessidades e permanecendo junto dela com
              solicitude amorosa.
            </C.Paragraph>
            <C.Paragraph>
              Assim  o nosso Deus, Ele nos olha com carinho e com amor nos
              convida a segui-lo. queres ento ver alguns exemplos de vocao!??
            </C.Paragraph>

            <C.Paragraph>
              <b>
                Jesus estava andando s margens do mar da Galilia e avistando
                Pedro e Andr disse-lhes: Segue-me (mt 4, 18-20).
              </b>{" "}
              Cristo o chama a lanar-se em guas mais profundas e avanar no
              mar que  Deus.
            </C.Paragraph>

            <C.Paragraph>
              Seduziste me Senhor e eu me deixei seduzir, foste mais forte (Jr
              20,7). No vale apena lutar contra a graa de Deus,  melhor
              seguir a inspirao do esprito e o sbio conselho dos salmos:Em
              silencio abandona-te ao Senhor(Sm36 7) ...como uma criana no
              colo materno e pe tua esperana no Senhor(Sm 130,2s).
            </C.Paragraph>

            <C.Paragraph>
              Que Ele saber te conduzir e assim a sua graa h de completar em
              ti a obra comeada. Poder enfim sentir-se como ns nos sentimos
              e que o salmo bem expressa:
            </C.Paragraph>

            <C.Paragraph>
              <b>JUNTO A VS SENHOR, FELICIDADE SEM LIMITES</b>
            </C.Paragraph>

            <b>
              Requisitos necessrios para ser uma Monja Concepcionista
              Franciscana
            </b>

            <ul>
              <li>Inteno sincera de Servir a Deus e vontade livre</li>
              <li>Idoneidade espiritual, moral e intelectual</li>
              <li>Sade fsica e psquica</li>
              <li>Maturidade pessoal</li>
              <li>Adequada formao humana e intelectual</li>
              <li>Desejo de pertencer totalmente  Deus</li>
              <li>Gosto pela orao e recolhimento</li>
              <li>de 18 a 28 anos de idade ( podem ter excees )</li>
              <li>Sade normal</li>
            </ul>
          </div>
        </div>
      </div>
    </>
  );
};

export default Vocation;
