import React from "react";
import Grid from "@mui/material/Grid";
import { Divider } from "@mui/material";
import Frei from "../../../_assets/img/pill/frei_galvao.jpg";
import ImageGallery from "react-image-gallery";
import "react-image-gallery/styles/css/image-gallery.css";
import { images } from "./photos";
import Benefactor1 from "../../../_assets/img/benefactor/benfeitores_1.jpg";

const Blog = () => {
  return (
    <>
      <div class="row d-flex justify-content-center">
        <div class="col-9">
          <h1 class="mb-5 text-center font-italic">
            <Divider>Blog</Divider>
          </h1>

          <h3 class="mb-5 text-center font-italic">
            Plulas de Frei Galvo no Mosteiro de Bauru
          </h3>

          <div class="d-flex">
            <div>
              <figure class="m-2">
                <img src={Frei} alt="" />
              </figure>
            </div>
            <div class="col-6 m-2">
              <p>
                As Irms do Mosteiro de Bauru, fornecem as plulas do Santo
                Antnio de SantAnna Galvo a todos os fiis devotos que delas
                quiserem implorar de Deus as graas desejadas, pela intercesso
                deste to querido e poderoso Intercessor, que durante a sua
                vida, sempre cheio do esprito de caridade, no media
                sacrifcios para aliviar os sofrimentos alheios.
              </p>

              <h3 class="fs-5 text-start">
                Horrio de atendimento no Mosteiro:
              </h3>

              <p>
                <b>Manh:</b> 09:00 s 11:30 <br />
                <b>Tarde:</b> 14:30 s 17:00 <br />
                <b>Telefone:</b> (14) 3265-1624
              </p>
            </div>
          </div>

          <h3 class="mb-5 text-center font-italic">Benfeitores do Mosteiro</h3>

          <h3 class="fs-5 text-start">Filhos espirituais de Santa Beatriz</h3>

          <div class="col-9">
            <p>
              As Monjas da Imaculada Conceio, manifestam um apreo e
              reconhecimento especial aos Amigos e Benfeitores do Mosteiro,
              participando de suas alegrias e provaes e recordando-os,
              particularmente, na Orao,formando assim,uma Famlia Espiritual
              no Corao de Santa Beatriz, diante do Altar do Senhor.
            </p>

            <h3 class="fs-5 text-start">
              Famlia dos Voluntrios e Benfeitores do Mosteiro Imaculada
              Conceio
            </h3>

            <div class="d-flex">
              <div>
                <figure class="text-start">
                  <img src={Benefactor1} class="" alt="" />
                </figure>
              </div>
              <div class="col-8 p-2">
                <p>
                  Os Voluntrios e Benfeitores do Mosteiro Imaculada Conceio
                  formam com as Irms, uma s Famlia que, centrados no Amor a
                  Deus,  Santssima Virgem Imaculada e a Santa Beatriz, buscam:
                </p>

                <p class="">
                  "Viver uma vida alicerada na Palavra de Deus e na prtica dos
                  Sacramentos, apoiados pelas Oraes, conselhos e
                  direcionamento espiritual das Irms, comprometendo-se assim
                  com a Caridade e o Bem do prximo."
                </p>
              </div>
            </div>

            <h3 class="fs-5 text-start">Atividades que desempenham</h3>
           
            <ul>
              <li>Auxiliam nas Campanhas em prol do Mosteiro, principalmente no objetivo da nova construo</li>
              <li>Representam as Irms em exposies</li>
              <li>Auxiliam nos Eventos promovidos pelo Mosteiro</li>
            </ul>

            <h3 class="fs-5 text-start pb-2">Galeria de fotos(festas dos anos 2005 - 2006 - 2007)</h3>

            <p class="col-8">
              <ImageGallery items={images} />
            </p>

          </div>
        </div>
      </div>

      <div class="row mb-3 d-flex justify-content-center"></div>
    </>
  );
};

export default Blog;
