import React from "react";
import Box from "@mui/material/Box";
import { Divider, Typography } from "@mui/material";
import Grid from "@mui/material/Grid";
import Fraternity from "../../_assets/img/fraternidade.jpg";

const Fraternidade = () => {
  return (
    <>
      <Box
        display="flex"
        marginBottom="50px"
        flexDirection="column"
        marginTop="30px"
      >
        <Box display="flex" justifyContent="center">
          <Grid container>
            <Grid item xs={12} sm={6} marginLeft="300px">
              <img src={Fraternity} />
            </Grid>
          </Grid>
          <Box
            sx={{
              display: "flex",
              flexDirection: "column",
              marginLeft: "50px",
            }}
          >
            <Grid item xs={12} sm={6} marginRight="350px">
              <Typography style={{ color: "#625422" }} variant="h3">
                Fraternidade
              </Typography>
              <Divider />
              <Typography
                marginTop={5}
                style={{ fontSize: "20px" }}
                variant="subtitle1"
              >
                A Vida Fraterna em comum  uma manifestao do Amor de Deus que
                Une as Irms entre si e as congrega em torno de Cristo, formando
                uma famlia , na qual cada Irm  um lugar privilegiado de
                Comunho com Deus.
                <br /> CCGG 95 O Mosteiro  uma Nazar feita de silncio,
                fraternidade, trabalho, alegria e paz.
              </Typography>
              <figure class="text-center fs-8">
                <blockquote>
                  <b>
                     Consagradas ao generoso Servio do Altssimo e da Virgem
                    Maria em honra da sua Imaculada Consceio.
                  </b>
                </blockquote>
              </figure>
            </Grid>
          </Box>
        </Box>
      </Box>
    </>
  );
};

export default Fraternidade;
