import React, { useState } from "react";
import PgFacebook from "../../../components/pgFacebook";
import ShowMoreText from "react-show-more-text";

import Santa1 from "../../../_assets/img/staBeatriz/santa1.jpg";
import Santa2 from "../../../_assets/img/staBeatriz/santa2.jpg";
import Santa3 from "../../../_assets/img/staBeatriz/santa3.jpg";
import Santa4 from "../../../_assets/img/staBeatriz/santa4.jpg";
import Santa5 from "../../../_assets/img/staBeatriz/santa5.jpg";
import Santa6 from "../../../_assets/img/staBeatriz/santa6.jpg";
import Santa7 from "../../../_assets/img/staBeatriz/santa7.jpg";
import Santa8 from "../../../_assets/img/staBeatriz/santa8.jpg";
import Santa9 from "../../../_assets/img/staBeatriz/santa9.jpg";
import Santa10 from "../../../_assets/img/staBeatriz/santa10.jpg";
import Santa11 from "../../../_assets/img/staBeatriz/santa11.jpg";
import Santa12 from "../../../_assets/img/staBeatriz/santa12.jpg";

const SantaBeatriz = () => {
  const [expand, setExpend] = useState(false);
  const onClick = () => {
    setExpend(!expand);
  };

  return (
    <>
      <div class="row">
        <div class="col-9">
          <div class="row">
            <h1 class="mb-5 text-center font-italic">Santa Beatriz</h1>
            <h3 class="mb-4 text-center font-italic">
              A Virgem singularmente amada de Maria Imaculada
            </h3>

            <div class="row mb-3">
              <div class="col-5 d-flex justify-content-end">
                <figure class="my-4">
                  <img src={Santa1} alt="" class="img-fluid" />
                </figure>
              </div>
              <div class="col-7" justify-content="left">
                <h4>Nascimento de Beatriz e sua Infncia</h4>
                <ShowMoreText
                  lines={12}
                  more={"Mostrar mais"}
                  less={"Mostrar menos"}
                  onClick={onClick}
                  expanded={expand}
                >
                  <p>
                    Beatriz nasceu em Ceuta  frica (que na poca era possesso
                    portuguesa) por volta do ano 1424 ou 1426 filha de Dom Rui
                    Gomes da Silva e Dona Isabel de Meneses era a segunda dentre
                    os onze filhos que nasceram neste abenoado lar.
                  </p>
                  <p>
                    Em 1433 D. Rui Gomes da Silva, pai de Beatriz , foi nomeado
                    pelo Rei de Portugal, alcaide da cidade de Campo Maior (
                    Cidade que se espalha aos ps de uma montanha, em frtil
                    regio regada pelos rios de Caia e Gvora, nas fronteiras de
                    Portugal e Espanha). Foi este o cenrio onde se desenrolou a
                    Infncia de Beatriz, serena e calma no seio de uma famlia
                    profundamente crist, como eram os nossos antepassados,
                    cercada por dez irmos. Como filha da nobreza, recebeu
                    juntamente com os irmos, esmerada formao, mormente
                    religiosa. Foram seus mestres os Religiosos Franciscanos
                    que, ao par de slida piedade, lhe incutiram um terno amor 
                    Maria Santssima, no mistrio da sua Imaculada Conceio.
                  </p>
                  <p>
                    A uma alma delicada unia Beatriz uma natureza cativante, e
                    deslumbrante formosura.
                  </p>
                </ShowMoreText>
              </div>
            </div>

            <div class="row mb-3">
              <div class="col-5 d-flex justify-content-end">
                <figure class="my-4">
                  <img src={Santa2} alt="" class="img-fluid" />
                </figure>
              </div>
              <div class="col-7" justify-content="left">
                <h4>Amor de Beatriz  Maria</h4>
                <ShowMoreText
                  lines={10}
                  more={"Mostrar mais"}
                  less={"Mostrar menos"}
                  onClick={onClick}
                  expanded={expand}
                >
                  <p>
                    Todos os seus Bigrafos narram sua grande piedade e virtude
                    desde sua infncia, e os testemunhos para o Processo de sua
                    Canonizao declaram que desde menina foi devotssima do
                    mistrio da Imaculada Conceio.
                  </p>
                  <p>
                    Madre Catarina de Santo Antonio, com grande efuso escreveu:
                  </p>
                  <p>
                    Para mim, no houve santo na Igreja de Deus, que no tocante
                    ao Mistrio da Imaculada Conceio, tenha feito a Deus to
                    grande servio como esta varonil mulher.
                  </p>
                  <p>
                    Como prova de seu amor e venerao  Virgem pode servir-nos
                    o seguinte acontecimento.  Querendo D. Rui Gomes ornar com
                    uma imagem da Virgem a Capela particular, mandou vir da
                    Itlia um afamado pintor. Chegando este, encantado com a
                    beleza da jovem Beatriz, quis t-la por modelo da Virgem.
                    Depois de muitos rogos, teve que obedecer s ordens de seu
                    pai, porem no conseguiram que abrissem seus olhos
                    encantadores. Assim o artista pode reproduzir as feies da
                    bela donzela, resultando num quadro de Maria com os olhos
                    semicerrados e de sublime modstia ladeada por So Francisco
                    e Santo Antnio . Este quadro encontra-se ainda hoje, na
                    Igreja da Misericrdia em Campo Maior
                  </p>
                  <p>
                    Temos, deste modo retratadas num quadro da Virgem, toda a
                    beleza e pureza da Jovem Beatriz que tanto desejava imitar 
                    Maria Santssima e de fato a imitou.
                  </p>
                </ShowMoreText>
              </div>
            </div>

            <div class="row mb-3">
              <div class="col-5 d-flex justify-content-end">
                <figure class="my-4">
                  <img src={Santa3} alt="" class="img-fluid" />
                </figure>
              </div>
              <div class="col-7" justify-content="left">
                <h4>Na Corte de Castela</h4>
                <ShowMoreText
                  lines={8}
                  more={"Mostrar mais"}
                  less={"Mostrar menos"}
                  onClick={onClick}
                  expanded={expand}
                >
                  <p>
                    Em 1447, a Infanta de Portugal Dona Isabel desposava o Rei
                    vivo D. Joo de Castela. O casamento real deu ocasio a
                    Dona Isabel de visitar a casa de D. Rui Gomes da Silva, em
                    Campo Maior. Encantada com as prendas morais e fsicas de
                    jovem Beatriz, Isabel quis t-la como primeira-dama de
                    honra. Com o consentimento dos pais, deixou Beatriz a sua
                    querida Campo Maior, e com sacrifcios abandonou o calmo
                    remanso da casa paterna para entrar no burburinho da Corte.
                  </p>
                  <p>
                    Como se l nas pginas de um escrito de 1526, Beatriz alm
                    de descender da alta nobreza, era uma donzela muito
                    graciosa, superando todas as companheiras em formosura e
                    graa. O encanto de Beatriz fascinava a quantos a
                    conheciam, muitos jovens a solicitavam em matrimnio; porm
                    Beatriz vivia em regies mais elevadas, alimentava-se de
                    anseios mais nobres. Dentro de si guardava um segredo:
                    DESEJAVA SER TODA DE DEUS
                  </p>
                  <p>
                    Os encantos fsicos e as qualidades morais de Beatriz
                    tornavam-na a favorita da Corte. O prprio Rei lhe dedicava
                    uma admirao particular, pois sentia necessidade e
                    encontrar, em meio a tanta falsidade, um corao reto que o
                    compreendesse e em quem pudesse confiar.
                  </p>
                </ShowMoreText>
              </div>
            </div>

            <div class="row mb-3">
              <div class="col-5 d-flex justify-content-end">
                <figure class="my-4">
                  <img src={Santa4} alt="" class="img-fluid" />
                  <figcaption>Ba onde foi encerrada</figcaption>
                  <figcaption>Santa Beatriz</figcaption>
                </figure>
              </div>
              <div class="col-7" justify-content="left">
                <h4>O Pecado de ser bela</h4>
                <ShowMoreText
                  lines={12}
                  more={"Mostrar mais"}
                  less={"Mostrar menos"}
                  onClick={onClick}
                  expanded={expand}
                >
                  <p>
                    Beatriz era bela e pudorosa. Esforava-se por manter sua
                    conduta irrepreensvel em meio a tantas frivolidades, cimes
                    e vaidades. Porm apesar de tudo, certos palacianos
                    maliciosos e maldosos a caluniaram de ter secretos encontros
                    amorosos com o Rei.
                  </p>
                  <p>
                    A isto no resistiu os cimes da Rainha Isabel, quando ouviu
                    os rumores da calnia, comeou a duvidar da fidelidade de
                    seu marido o Rei e viu em Beatriz uma possvel rival. Queria
                    ento faz-la desaparecer. Comeou a arquitetar uma maneira
                    de faz-la desaparecer. O cime  sempre engenhoso ... Certa
                    noite a Rainha convidou Beatriz para que a seguisse e
                    simulando querer contar-lhe um segredo, a levou por um
                    solitrio caminho do palcio at chegar num lugar que tinha
                    preparado de antemo. Ao passar junto dele, a Rainha lhe deu
                    um forte empurro fazendo-a cair dentro do grande ba, e
                    cerrando-o rapidamente com chave, abandonou-a na escurido.
                  </p>
                  <p>
                    Beatriz gritou, suplicou ... Era inocente nunca dera ouvidos
                    aos galanteios, nem salientara ambio alguma. Mas o cime
                    da Rainha j se transformara em dio e o dio  cego.
                    Beatriz estava sepultada viva, as trevas a cercavam e
                    faltava-lhe todo o auxlio humano. Condenada a morrer
                    asfixiada dentro de um ba! Era jovem e inocente. O
                    desespero rondava-lhe a alma.
                  </p>

                  <p>
                    Da Terra nada podia esperar. Voltou-se ento para o Cu.
                    Entregou-se nas mos de Deus e se encomendou  Sesma Virgem
                    com grande devoo. A vida parecia fugir, quando se viu
                    envolta em vivssima luz. E nesta luz divisou a Virgem
                    Santssima, resplandecente como os raios do sol, vestida com
                    hbito branco e manto azul celeste, em seus braos um
                    formoso Menino, ferindo com uma lana a cabea de um
                    horrendo drago. Depois de confort-la com carinho maternal
                    lhe disse:
                  </p>
                  <p>
                    - Beatriz, quero que fundes uma nova Ordem que glorifique
                    minha Conceio Imaculada, vestindo Hbito branco e manto
                    azul celeste como trago...
                  </p>
                  <figure class="my-4">
                    <img src={Santa5} alt="" class="img-fluid" />
                  </figure>
                  <p>
                    Beatriz agradecida, se entregou com serva e escrava, e
                    consagrando sua virgindade se ofereceu em corpo e alma ao
                    servio de sua Celestial Senhora a Me de Deus. A Virgem
                    Santssima, depois de prometer-lhe que sairia daquele ba
                    com vida, desapareceu, deixando Beatriz numa felicidade
                    indescritvel.
                  </p>
                  <p>
                    Passados, porm, trs dias, o Tio de Beatriz exigiu
                    explicaes da Rainha. Esta levou-a ao lugar onde estava
                    encerrada Beatriz. Levava a certeza de encontr-la morta, j
                    cadver, pois ningum resistiria a tal suplcio por muito
                    tempo. Teriam terminado, assim, as rivalidades. Mas terrvel
                    surpresa a aguardava. Ao abrir o ba, em lugar de um cadver
                    hirto e lvido, sem vida, surgiu Beatriz, risonha ...
                    formosa ... cheia de vida ... quase resplandecente. Parecia
                    mais bela, com um sorriso celeste a emoldurar lhe o doce
                    semblante.
                  </p>
                  <p>
                    Dois sentimentos chocaram-se no corao da Rainha: a raiva e
                    o pavor. Entre atnita e apavorada, desfaz-se em lgrimas e
                    cai aos ps da donzela com rogos de perdo. Um abrao foi a
                    resposta de Beatriz.
                  </p>
                  <p>
                    A Rainha que arquitetara o desaparecimento da sua rival,
                    sentiu-se humilhada ao presenciar que o seu ato apenas
                    servira para aumentar o prestgio da sua vtima. E passados
                    os primeiros momentos de emoo, Isabel sentiu a inveja
                    acordar em seu corao. Ordenou a Beatriz que abandonasse a
                    Corte.
                  </p>
                  <p>
                    Para Beatriz, uma alegria, no sentia outro desejo. Trs
                    dias depois acompanhada pelo seu Tio, abandonou a Corte e
                    dirigiu seus passos a Toledo.
                  </p>
                </ShowMoreText>
              </div>
            </div>

            <div class="row mb-3">
              <div class="col-5 d-flex justify-content-end"></div>
              <div class="col-7" justify-content="left">
                <h4>A Viagem</h4>
                <ShowMoreText
                  lines={3}
                  more={"Mostrar mais"}
                  less={"Mostrar menos"}
                  onClick={onClick}
                  expanded={expand}
                >
                  <p>
                    O caminho a percorrer era de mais ou menos 230 quilmetros.
                    Cheio de perigos. Guerrilheiros se ocultavam nos bosques e
                    salteadores espreitavam nas gargantas dos vales.
                  </p>
                  <figure class="my-4">
                    <img src={Santa6} alt="" class="img-fluid" />
                  </figure>
                  <p>
                    A certa altura da viagem, de um bosque que ladeava o
                    caminho, saram dois Frades de Hbito marrom e cordo. Dois
                    Franciscanos. Os Frades, porm, a saudarame um deles falava
                    a lngua portuguesa. Mais tranquilizada, perguntou-lhes
                    Beatriz: Quem sois? Donde vindes?
                  </p>
                  <p>
                    - Tranquilizai-vos. Queremos dar-vos a notcia que estais a
                    caminho para vos tornardes uma das maiores damas da Espanha.
                    E vossas filhas espalhar-se-o por toda a Terra.
                  </p>
                  <p>
                    - Impossvel. Ofereci a Deus minha virgindade consagrando-me
                    inteiramente a Ele, jamais casaria com homem algum.
                  </p>
                  <p>O que dissemos acontecer.  Disseram eles.</p>
                  <p>
                    Enquanto ela refletia, desapareceram. Beatriz compreendeu
                    que se tratava de uma mensagem do Cu e que os dois
                    Franciscanos outros no eram que So Francisco de Assis e
                    seu patrcio Santo Antnio de Lisboa, a quem ela era muito
                    devota.
                  </p>
                </ShowMoreText>
              </div>
            </div>

            <div class="row mb-3">
              <div class="col-5 d-flex justify-content-end">
                <figure class="my-4">
                  <img src={Santa7} alt="" class="img-fluid" />
                </figure>
              </div>
              <div class="col-7" justify-content="left">
                <h4>No Mosteiro de Santo Domingo o Real</h4>
                <ShowMoreText
                  lines={3}
                  more={"Mostrar mais"}
                  less={"Mostrar menos"}
                  onClick={onClick}
                  expanded={expand}
                >
                  <p>
                    Chegando pr fim a Toledo e desejosa e paz e santidade,
                    dirigiu-se ao Mosteiro de Santo Domingo o Real.
                  </p>
                  <p>
                    A Abadessa lhe abriu as pesadas portas do Mosteiro com
                    admirao e lhe conduziu at sua cela.
                  </p>
                  <p>
                    Toma uma deciso. O seu rosto que causou tantos transtornos
                    no palcio e que provocou tantas calnias no dio mortal da
                    Rainha, cobriu-o com um vu. Este rosto com toda a sua
                    beleza, totalmente consagrado  Deus. E cobriu o rosto com
                    um vu. Ali morou uns trinta anos em qualidade de secular,
                    repartindo sua vida entre trabalho e a orao, sendo um
                    preclaro exemplo de honestidade e virtude. Grande parte da
                    noite passava em orao junto ao Sacrrio, donde cresceu em
                    amor  Jesus, e ao mistrio da Imaculada.
                  </p>
                  <p>
                    Certo tempo depois, morreu o Rei, e a Rainha arrependida do
                    que havia feito com Beatriz, reconhecendo sua inocncia, foi
                    a Toledo v-la e pedi-la perdo. Beatriz, que no lhe tinha
                    guardado ressentimento, no s a perdoou, esquecendo-se
                    completamente o sucedido. Tambm a visitavam com sua me a
                    Rainha, os Infantes Afonso e Isabel, a que mais tarde seria
                    a Rainha Isabel a Catlica, que manteve grande amizade com
                    Beatriz. Religiosas contemporneas de Beatriz assim deixaram
                    escrito: ... Beatriz teve uma vida de silncio, de orao,
                    de muita humildade, de penitncia e austeridade, mulher de
                    grande F, modelo exemplar de virtude para todos.
                  </p>
                </ShowMoreText>
              </div>
            </div>

            <div class="row mb-3">
              <div class="col-5 d-flex justify-content-end">
                <figure class="my-4">
                  <img src={Santa8} alt="" class="img-fluid" />
                </figure>
              </div>
              <div class="col-7" justify-content="left">
                <h4> Chegado o Momento</h4>
                <ShowMoreText
                  lines={10}
                  more={"Mostrar mais"}
                  less={"Mostrar menos"}
                  onClick={onClick}
                  expanded={expand}
                >
                  <p>
                    Uma tarde, depois da orao, quando todas as Monjas haviam
                    deixado o Coro, Beatriz em ntimo colquio com Deus,
                    profundamente envolvida na orao, quando um grande
                    resplendor a iluminou e viu a Santssima Virgem com as
                    mesmas vestes, que trazia, quando a viu no Ba uns trinta
                    anos atrs. A Santssima Virgem estava sorridente, com o
                    Menino em seus braos lhe disse em uma mescla de amor e
                    firmeza:
                  </p>
                  <p>
                      Filha,  chegado o momento. Ergue-te e pe em execuo a
                    obra a ti confiada. Vai e glorifica a Deus com esta obra.
                    Meu Filho e eu estaremos contigo.
                  </p>
                  <p>
                    Beatriz entendeu que soara a hora de Deus, deveria partir
                    para o combate, fixando o olhar em Jesus Crucificado,
                    aceitou todos os trabalhos aos quais sabia que viriam, e
                    pronunciando seu FIAT se disps a cumprir sem demora os
                    desejos da Santssima Virgem.
                  </p>
                </ShowMoreText>
              </div>
            </div>

            <div class="row mb-3">
              <div class="col-5 d-flex justify-content-end">
                <figure class="my-4">
                  <img src={Santa9} alt="" class="img-fluid" />
                </figure>
              </div>
              <div class="col-7" justify-content="left">
                <h4>Em Santa F</h4>
                <ShowMoreText
                  lines={5}
                  more={"Mostrar mais"}
                  less={"Mostrar menos"}
                  onClick={onClick}
                  expanded={expand}
                >
                  <p>
                    Enquanto Beatriz, recolhida no Mosteiro de So Domingos
                    aguardava a hora que Deus determinara para dar incio  sua
                    obra, o trono da Espanha era ocupado pela Rainha Isabel, a
                    Catlica. Filha da outra Rainha Isabel.
                  </p>
                  <p>
                    Esta Rainha Isabel a Catlica foi ntima amiga de Beatriz,
                    sua principal confidente e colaboradora. Duas genunas
                    filhas de So Francisco de Assis. Dois coraes voltados
                    para o alto que se compreendiam perfeitamente. Ali no
                    locutrio do Mosteiro a Rainha falava dos negcios do Reino
                    que tanto a preocupavam, e Beatriz lhe falava de seus ideais
                    e inspiraes.
                  </p>
                  <p>
                    A Rainha Isabel entusiasmada por to bela misso em fundar
                    uma Ordem que glorificasse a Imaculada Conceio de Nossa
                    Senhora, prometeu-lhe ajuda e para que Beatriz desse incio
                     fundao, ofereceu-lhe parte dos Palcios de Galiana, no
                    muito longe do Mosteiro de So Domingos. E deu-lhe tambm a
                    Igreja dedicada  Virgem Mrtir Santa F. Bela invocao
                    para quem ia se lanar em um to importante empreendimento!
                  </p>
                  <p>
                    Cheia de confiana e F, acompanhada de 12 jovens, deixou
                    Beatriz o Mosteiro de So Domingos e recolheu-se nos
                    Palcios de Galiana adaptado para a Vida Claustral e Igreja
                    Santa F. Exercida por to longo tempo na virtude, podia
                    agora tornar-se Mestra e Me. E de fato, comeou a dirigir
                    suas 12 companheiras no caminho da perfeio e da Vida
                    Religiosa. E o fazia mais pelo exemplo que pela palavra. E
                    isso tinha grande valor, porque se a palavra tem a fora de
                    comover; o exemplo tem a virtude de atrair.
                  </p>
                  <p>
                    O pequeno grupo comeava a ensaiar os primeiros passos da
                    Vida Concepcionista. Lenta, mas seguramente sob a direo
                    sbia de Beatriz.
                  </p>
                  <p>
                    Enquanto isso, a Rainha Isabel escrevia ao Papa, procurando
                    obter a aprovao da nova Famlia Religiosa que nascia sob
                    as benes da Imaculada.
                  </p>
                </ShowMoreText>
              </div>
            </div>

            <div class="row mb-3">
              <div class="col-5 d-flex justify-content-end">
                <figure class="my-4">
                  <img src={Santa10} alt="" class="img-fluid" />
                  <figcaption>Bula Inter Universa</figcaption>
                  <figcaption>de 30 de abril de 1489</figcaption>
                </figure>
              </div>
              <div class="col-7" justify-content="left">
                <h4>O Papa Inocncio VIII Aprova a Nova Ordem</h4>
                <ShowMoreText
                  lines={9}
                  more={"Mostrar mais"}
                  less={"Mostrar menos"}
                  onClick={onClick}
                  expanded={expand}
                >
                  <p>
                    O Papa Inocncio VIII, pela Bula Inter Universa no dia 30 de
                    abril de 1489, aprovava a Ordem Concepcionista. No mesmo
                    instante em que a Bula era firmada pelo Papa, Beatriz
                    recebeu a notcia por um emissrio do Cu, que ela intuiu
                    fosse o Arcanjo So Rafael, de quem fora sempre devotssima.
                    E anunciou que ela seria expedida para Toledo, com seu
                    emissrio por navio.
                  </p>
                  <p>
                    Fcil imaginar a alegria da Fundadora e de suas
                    companheiras. Mas a esta alegria sucedeu uma grande dor.
                    Passados alguns dias, chegou-lhe a triste notcia que o
                    navio onde viajavam os portadores da Bula havia naufragado,
                    apenas se haviam salvado alguns tripulantes. Tudo o mais
                    perecera no mar. Ferida por to rude golpe, prostrou-se
                    Beatriz, imersa em grande dor, diante do Santssimo
                    Sacramento e por trs dias, esteve muito aflita pedindo a
                    Deus soluo para este problema.
                  </p>
                  <p>
                    Passados trs dias, a calma j dominava o esprito de
                    Beatriz. Pensava Deus cuidaria que o Papa expedisse outra
                    Bula e cuidaria tambm que est lhe chegasse s mos. Mas
                    indo um cofre onde conservava objetos do seu uso, encontrou,
                    sobre ele, um pergaminho que nunca vira ali. Desenrolou-o e
                    notou ser um documento Pontifcio para sua grande surpresa e
                    admirao se achava ali a Bula to desejada. Mas como viera
                    parar ali? Sem dvida, mais uma delicadeza da Providncia
                    Divina, mais uma interveno bondosa de Deus, por meio da
                    Imaculada Conceio. O Cu velava em mostrar o agrado que
                    nutria por esta obra.
                  </p>
                  <p>
                    Madre Beatriz via finalmente coroados seus esforos. Em nome
                    de Deus o Papa confirmava solenemente a Ordem da Imaculada
                    Conceio de Nossa Senhora.
                  </p>
                </ShowMoreText>
              </div>
            </div>

            <div class="row mb-3">
              <div class="col-5 d-flex justify-content-end">
                <figure class="my-4">
                  <img src={Santa11} alt="" class="img-fluid" />
                </figure>
              </div>
              <div class="col-7" justify-content="left">
                <h4>Crucificada com Cristo</h4>
                <ShowMoreText
                  lines={7}
                  more={"Mostrar mais"}
                  less={"Mostrar menos"}
                  onClick={onClick}
                  expanded={expand}
                >
                  <p>
                    Madre Beatriz e as doze jovens preparavam  se para o dia da
                    Consagrao definitiva. Cada dia que passava era para
                    Beatriz mais um motivo de jbilo, pois a aproximava do
                    grande momento. Mas Deus havia disposto as coisas de modo
                    diferente. Faltavam somente dez dias para o solene dia da
                    tomada de Hbito e Profisso das Religiosas e como elo da
                    definitiva inaugurao da Nova Ordem lhe aparece a
                    Santssima Virgem e com voz amorosa lhe diz:
                  </p>
                  <p>
                      Minha filha, de hoje h dez dias, virei buscar-te para
                    estares comigo no Cu, por que no  vontade de Deus, que
                    gozes aqui na Terra disto que tanto desejas. E
                    mostrando-lhe a lmpada que ardia diante do Santssimo , que
                    depois de apagada por um momento voltava a brilhar lhe disse
                    :
                  </p>
                  <p>
                      Viste como a luz da lmpada se apagou e logo voltou a
                    brilhar ? Pois assim se passar com tua Ordem. Ao morreres,
                    a Ordem se dissolver, mas logo volver a renascer e
                    florescer de tal maneira que ser multiplicada e se
                    estender pelo mundo inteiro . Ter o desespero batido 
                    porta do corao de Beatriz? No absolutamente. Calma e
                    serena chamou o seu Confessor a fim de preparar-se
                    dignamente para a partida. Logo caiu enferma, e a
                    enfermidade a prostrou no leito. E foi ali, no leito de
                    dores que revestiu o Hbito Concepcionista e emitiu os Votos
                    Solenes de viver sempre Obediente, sem Propriedade, em
                    Castidade, com perptua Clausura.
                  </p>
                  <figure class="my-4">
                    <img src={Santa12} alt="" class="img-fluid" />
                  </figure>
                  <p>
                    E assim unida inseparavelmente a Jesus Cristo, revestida com
                    o Hbito da Conceio, aguardava serena, em seu leito, a
                    ordem de partir. Fizera voto de obedincia e partiria ao
                    primeiro aceno do Esposo Divino. Que melhor altar para se
                    entregar a Deus que o leito de dores? Que maior pompa que a
                    pobreza de uma cela de Mosteiro? Que mais comovente msica
                    que os soluos das filhas que a rodeiam?
                  </p>
                  <p>
                    Em sua agonia, cercada por suas filhas espirituais e 6
                    Franciscanos, ao administr-la os ltimos Sacramentos, e ao
                    levantar-se o vu que cobria seu rosto para dar-lhe a Santa
                    Uno, todos se admiravam da formosura de seu rosto que
                    brilhava como um anjo, porm a admirao cresceu ainda mais
                    ao aparecer uma brilhante estrela em sua fronte. Beatriz
                    acabava se entregar sua bendita alma  Deus.
                  </p>
                  <p>
                    A Primeira Concepcionista recebia do Esposo o Convite: Vem
                    minha esposa, recebe a coroa que o Senhor te reservou desde
                    toda a eternidade. Como para os Santos a morte no  uma
                    calamidade, mas uma ventura, pois os leva ao porto por cuja
                    posse lutaram.
                  </p>
                </ShowMoreText>
              </div>
            </div>

            <div class="row mb-3">
              <div class="col-5 d-flex justify-content-end"></div>
              <div class="col-7" justify-content="left">
                <h4>Primeiras Glrias</h4>
                <ShowMoreText
                  lines={20}
                  more={"Mostrar mais"}
                  less={"Mostrar menos"}
                  onClick={onClick}
                  expanded={expand}
                >
                  <p>
                    A primeira canonizao recebeu-a Beatriz do povo. Beatriz
                    morreu com grande fama de santidade. Na transladao feita
                    em janeiro de 1515 sucedeu a cura de um cego de nascena, ao
                    aproximar-se da urna com os restos venerandos, a devoo e o
                    culto cresceram. Foi Beatificada no dia 28 de julho de 1926
                    pelo Papa Pio XI e canonizada no dia 03 de outubro de 1976
                    pelo Papa Paulo VI, contava a Ordem mais de 150 Mosteiros
                    espalhados pela Europa e Amrica.
                  </p>
                  <p>
                    Pelos sculos segue brilhando do Cu a Estrela da Ordem da
                    Imaculada Conceio e sua luz continua iluminando suas
                    Filhas e a todos os cristos que a invocam com devoo.
                  </p>
                  <br></br>
                  <br></br>
                </ShowMoreText>
                <p class="fw-bolder fst-italic">
                  Santa Madre Beatriz, rogai por ns !
                </p>
              </div>
            </div>
          </div>
        </div>
        <div class="col-3"></div>
      </div>
    </>
  );
};

export default SantaBeatriz;
